"use client"

import { Badge } from "@/components/ui/badge"

interface PlatformBadgesProps {
  platforms: string[]
}

const platformColors: Record<string, string> = {
  instagram: "bg-pink-500/10 text-pink-500",
  youtube: "bg-red-500/10 text-red-500",
  twitter: "bg-blue-500/10 text-blue-500",
  tiktok: "bg-purple-500/10 text-purple-500",
  twitch: "bg-violet-500/10 text-violet-500",
}

export function PlatformBadges({ platforms }: PlatformBadgesProps) {
  return (
    <div className="flex flex-wrap gap-2">
      {platforms.map((platform) => (
        <Badge key={platform} variant="secondary" className={platformColors[platform] || "bg-secondary"}>
          {platform.charAt(0).toUpperCase() + platform.slice(1)}
        </Badge>
      ))}
    </div>
  )
}
