import { Button } from "@/components/ui/button"
import { Badge } from "@/components/ui/badge"
import { Avatar, AvatarFallback, AvatarImage } from "@/components/ui/avatar"
import { CheckCircle, XCircle, Clock, MessageSquare, ExternalLink } from "lucide-react"
import { PlatformBadges } from "./platform-badges"

interface Application {
  id: number
  influencer: {
    name: string
    username: string
    avatar: string
    followers: string
    engagement: string
    platforms: string[]
  }
  campaign: string
  appliedDate: string
  status: "pending" | "accepted" | "rejected"
  message: string
}

const statusColors = {
  pending: "bg-yellow-500/10 text-yellow-500 border-yellow-500/20",
  accepted: "bg-green-500/10 text-green-500 border-green-500/20",
  rejected: "bg-red-500/10 text-red-500 border-red-500/20",
}

const statusIcons = {
  pending: <Clock className="h-4 w-4" />,
  accepted: <CheckCircle className="h-4 w-4" />,
  rejected: <XCircle className="h-4 w-4" />,
}

export function ApplicationCard({ application }: { application: Application }) {
  return (
    <div className="glass-card rounded-xl p-6 border border-border/50">
      <div className="space-y-4">
        {/* Header with influencer info and status */}
        <div className="flex flex-col gap-4 sm:flex-row sm:items-start sm:justify-between">
          <div className="flex gap-4">
            <Avatar className="h-16 w-16">
              <AvatarImage
                src={application.influencer.avatar || "/placeholder.svg"}
                alt={application.influencer.name}
              />
              <AvatarFallback>{application.influencer.name.charAt(0)}</AvatarFallback>
            </Avatar>
            <div className="flex-1">
              <div className="flex items-center gap-2">
                <h3 className="font-semibold text-foreground">{application.influencer.name}</h3>
                <Badge variant="outline" className="text-xs">
                  {application.influencer.username}
                </Badge>
              </div>
              <div className="mt-2 flex flex-wrap gap-4 text-sm text-muted-foreground">
                <span>{application.influencer.followers} followers</span>
                <span>{application.influencer.engagement} engagement</span>
              </div>
              <div className="mt-2">
                <PlatformBadges platforms={application.influencer.platforms} />
              </div>
            </div>
          </div>
          <Badge variant="outline" className={`whitespace-nowrap border ${statusColors[application.status]}`}>
            <span className="mr-1">{statusIcons[application.status]}</span>
            {application.status.charAt(0).toUpperCase() + application.status.slice(1)}
          </Badge>
        </div>

        {/* Campaign and date */}
        <div className="flex flex-col gap-2 border-t border-border/50 pt-4 text-sm">
          <div className="text-muted-foreground">
            <span className="font-medium text-foreground">Campaign:</span> {application.campaign}
          </div>
          <div className="text-muted-foreground">
            <span className="font-medium text-foreground">Applied:</span> {application.appliedDate}
          </div>
        </div>

        {/* Message */}
        <div className="rounded-lg bg-secondary/50 p-3 text-sm italic text-muted-foreground">
          {`"${application.message}"`}
        </div>

        {/* Actions */}
        <div className="flex flex-wrap gap-2 border-t border-border/50 pt-4">
          {application.status === "pending" && (
            <>
              <Button size="sm" className="flex-1 sm:flex-none">
                <CheckCircle className="mr-2 h-4 w-4" />
                Accept
              </Button>
              <Button size="sm" variant="outline" className="flex-1 sm:flex-none bg-transparent">
                <XCircle className="mr-2 h-4 w-4" />
                Reject
              </Button>
            </>
          )}
          <Button size="sm" variant="outline" className="flex-1 sm:flex-none bg-transparent">
            <MessageSquare className="mr-2 h-4 w-4" />
            Message
          </Button>
          <Button size="sm" variant="ghost" className="flex-1 sm:flex-none">
            <ExternalLink className="mr-2 h-4 w-4" />
            Profile
          </Button>
        </div>
      </div>
    </div>
  )
}